import React from "react"
import {Helmet} from "react-helmet"
import { graphql, useStaticQuery } from "gatsby"

const Head = (meta) => {

  const data = useStaticQuery(graphql`
  query {
    site {
      siteMetadata {
        title
        description
      }
    }
  }`)


  const defaultProps = {
    lang: `en`,
    meta: [],
    description: ``,
  }

  const metaDescription = meta.description || data.site.siteMetadata.description
  console.log(meta)
  return (
    <Helmet
      title={`${meta.title}` | `${data.site.siteMetadata.title}`}
      htmlAttributes={{
        defaultProps,
      }}
      meta={[
        {
          name: `description`,
          content: metaDescription,
        },
        {
          name: "keywords",
          content: "site.siteMetadata.keywords.join()",
        },
      ]}
    />
  )
}

export default Head